import { useCompleted } from "../Context/CompletedContext";
import { useNumber } from "../Context/NumberContext";
import { useScores } from "../Context/ScoresContect";
import {
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalFooter,
  ModalHeader,
  ModalOverlay,
  Button,
} from "@chakra-ui/react";
import { useTime } from "../Context/TimeContext";

function EndModal() {
  const { scores, setScores } = useScores();
  const { setNumber } = useNumber();
  const { completed, setCompleted } = useCompleted();
  const { setTimeLeft } = useTime();

  const onClose = function () {
    setScores(0);
    setNumber(0);
    setTimeLeft(30);
    setCompleted(false);
  };

  return (
    <Modal onClose={onClose} size="md" isOpen={completed}>
      <ModalOverlay />
      <ModalContent>
        <ModalHeader>Quiz Completed</ModalHeader>
        <ModalCloseButton />
        <ModalBody>You have scored {scores} marks.</ModalBody>
        <ModalFooter>
          <Button onClick={onClose}>Close</Button>
        </ModalFooter>
      </ModalContent>
    </Modal>
  );
}

export default EndModal;
