import { useNumber } from "../Context/NumberContext";
import { Box, Button } from "@chakra-ui/react";
import { useQuestions } from "../Context/QuestionsContext";
import { useScores } from "../Context/ScoresContect";
import { useChoice } from "../Context/ChoiceContext";
import { useCompleted } from "../Context/CompletedContext";
import { useTime } from "../Context/TimeContext";
import { useEffect, useRef } from "react";

function ProceedButton() {
  const { questions, fetchError } = useQuestions();
  const { choice, setChoice } = useChoice();
  const { number, setNumber } = useNumber();
  const { setCompleted } = useCompleted();
  const { setScores } = useScores();
  const { timeLeft, setTimeLeft } = useTime();
  const proceedButtonRef = useRef();

  const clickHandler = function (e) {
    const buttonPurpose = proceedButtonRef.current.textContent;
    if (questions[number].Correct === choice) setScores((prev) => prev + 1);

    if (buttonPurpose === "Next") {
      setNumber(number + 1);
      setTimeLeft(30);
    } else {
      setCompleted(true);
      setTimeLeft(0);
    }
    setChoice(null);
  };

  useEffect(() => {
    if (timeLeft === 0) proceedButtonRef.current.click();
  }, [timeLeft]);

  if (!questions || fetchError)
    return <Button colorScheme="teal" px="50" isDisabled>Next</Button>;

  return (
    <Box>
      <Button
        colorScheme="teal"
        px="50"
        onClick={clickHandler}
        ref={proceedButtonRef}
      >
        {number < questions.length - 1 ? "Next" : "Submit"}
      </Button>
    </Box>
  );
}

export default ProceedButton;
