import { useQuestions } from "../Context/QuestionsContext";
import {
  Stack,
  Heading,
  Text,
  Radio,
  RadioGroup,
  Spinner,
  AbsoluteCenter
} from "@chakra-ui/react";
import { useChoice } from "../Context/ChoiceContext";
import { useNumber } from "../Context/NumberContext";

function Question() {
  const { questions, fetchError } = useQuestions();
  const { number } = useNumber();
  const { choice, setChoice } = useChoice();

  if (!questions || questions.length === 0 || fetchError) {
    return (
      <AbsoluteCenter axis="both">
        <Spinner
        thickness="2px"
        speed="0.65s"
        emptyColor="gray.200"
        color="teal.500"
        size="xl"
      />
      </AbsoluteCenter>
    );
  }

  return (
    <>
      <Heading fontSize="2xl" as="h1" mt="2" mb="30">
        {number + 1}. {questions[number].Question}
      </Heading>
      <RadioGroup
        onChange={setChoice}
        value={choice}
        name={`Question_${number}`}
        colorScheme="teal"
      >
        <Stack>
          {questions[number].Options.map((option, index) => (
            <Radio value={option} key={`${number}.${option}.${index}`}>
              <Text fontSize="xl">{option}</Text>
            </Radio>
          ))}
        </Stack>
      </RadioGroup>
    </>
  );
}

export default Question;
