import { Container, Grid } from "@chakra-ui/react";

function QuizContainer({ children }) {

  return (
    <Container
      maxW="900px"
      bg="whiteAlpha.600"
      p="50"
      border="1px"
      borderColor="blackAlpha.50"
      borderRadius="10px"
      boxShadow="2xl"
    >
      <Grid templateRows="1fr 20px 6fr 1px 1fr" gap="20px">
        {children}
      </Grid>
    </Container>
  );
}

export default QuizContainer;
